.LegalEntity {

    

    .supportBlock {
        display: flex;
        justify-content: space-evenly;
        color: white;
        background-color: rgba(69, 69, 69, 0.8);
        text-align: center;
        padding: 1rem;
        margin-top: -3px;
        width: 100%;


        span {
            font-weight: bold;
            font-size: 20px;
        }
        p {
            margin-top: 0;
        }
    }
    .receivingPaymentsBlock {
        max-width: 1400px;
        padding: 1rem;
        text-align: center;
        margin: 1rem auto;

    }
    .payVariantsBlock {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        margin-top: 2rem;

    }
    .payVariantsItem {
        display: flex;
        flex-direction: column;
        max-width: 500px;
    }
    .itemImgWrap {
        height: 187px;
    }
    .variant {
        flex-grow: 1;
        margin-bottom: 1rem;
    }
    .detailsLink {
        font-weight: bold;
        color: #807CFA;
        transition: all 0.2s ease-in-out;

        &:hover {
            color: #A31FED;
        }

    }

    .productsBlock {
       max-width: 1400px;
       margin: 1rem auto;

    }
    .header {
        text-align: center;

        h2 {
            color: gray;
            font-weight: bold;
            font-size: 18px;
        }
    }
    .products {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
    .productsItem {
        text-align: left !important;
        align-self: flex-start !important;
        padding-left: 4rem !important;
    }

    @media (max-width: 900px) {
        .productsItem {
            text-align: center !important;
            align-self: center !important;
            padding-left: 1rem !important;
        }
    }

    @media (max-width: 600px) {
        .supportBlock {
            flex-direction: column;
            width: 95%;
        }
      
    }

    @media (max-width: 500px) {
        .supportBlock {
            position: absolute;
            bottom: 0;
        }
        .centeredBlockStyle {
            height: 400px;
        }
    }

    



}