.GrayCardWithImg {

  display: flex;
  flex-direction: column;
  // justify-content: space-between;
  width: 40%;
  max-width: 640px;  
  background-color:  #5B5A59;
  border-radius: 20px;
  margin: 1rem;
  transition: all 0.2s ease-in-out;
  color: white;

  .cardBody {
    display: flex;
    flex-direction: column;
    align-self: flex-end;
    text-align: right;
    max-width: 300px;
    padding: 1rem;
    height: 260px;
    padding-right: 4rem;
  }

  .btn {
      color: white;
      // align-self: center;
  }

  .img {
      width: 100%;
      border-radius: 0 0 15px 15px;
  }

  .text {
      flex-grow: 1;
  }

  @media (max-width: 900px){
    .cardBody {
      align-self: center;
      text-align: center;
      padding-right: 1rem;
    }
  }


  @media (max-width: 615px) {
    width: 100%;
    .cardBody {

      height: auto;
    }
  }

}