.InfoBlock {
    position: relative;
    margin-top: 2rem;
    overflow: hidden;

    .relativeBlock {
        position: relative;
        overflow: hidden;
    }

    .image {
        width: 100%;
    }
    .infoBlockText {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        z-index: 99;
        max-width: 640px;
        min-height: 450px;
        background-color: rgba(70, 70, 70, 0.9);
        color: white;
        padding: 5rem;

        p {
            max-width: 450px;
        }


    }
   
    .infoNav {
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 100px;
        left: 20%;
        width: 150px;
    }
    .infoNavItem {
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid gray;
        background-color: white;
        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            background-color: #03EE65;
        }
    }
    .active {
        background-color: #03EE65;
    }

    @media (max-width: 1300px) {
        .infoBlockText {
            position: static;
            transform: unset;
            margin: 0 auto;
            margin-top: -3px;
            min-height: auto;
            padding: 3rem;

            h1 {
                text-align: center;
            }
           
        }
        .paragraphs, .btnBlock {
            margin: 0 auto;
        }
        .infoNav  {
            left: 50%;
            bottom: 10%;
            transform: translateX(-50%);
        }
    }


    @media (max-width: 900px) {

        .infoBlockText {
            min-height: auto;
            padding: 1rem;

            h1 {
                font-size: 17px;
            }
            p {
                font-size: 14px;
            }
        }

    }



}