.SolutionsCard {
    display: flex;
    // flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    // background-color: white;

    // border: 2px gray solid;
    // margin: 1rem;
    padding: 1rem;
    border-radius: 28px;
    width: 100%;
    max-width: 600px;
    transition: all 0.2s ease-in-out;


    .SolutionsCardText {
        width: 60%;
        text-align: justify;

        h1 {
            text-align: left;
            font-size: 26px;
        }
    }

    @media (max-width: 500px) {
        flex-wrap: wrap;
       
        .SolutionsCardText {
            width: 100%;
        }
    }


    @media (max-width: 900px) {
        
        margin: 0;
    }

    // @media (max-width: 1500px) {
    //         width: 100%;
    //         margin-right: 0rem;
    // }








}