.SelfEmployed {


    .centered {
        text-align: center;
        margin-top: 4rem;
    }
    .youLikeIt {
        text-align: center;
        color: #A31FED;
        margin-top: 4rem;
    }
    .stepImgBlock {
        position: relative;
        h1 {
            position: absolute;
            font-size: 24px;
            color: white;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            white-space: nowrap;
        }
    }
    .paymentStepsImg {
        width: 100%;
    }
    .paymentStepsWrap {
        background-color: #464646;
        margin-top: -3px;
        padding: 1rem;
    }
    .paymentSteps {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        max-width: 1400px;
        margin: 0 auto;
        color: white;

    }
    .step {
        display: flex;
        max-width: 250px;

        span {
            display: block;
            margin-bottom: 1rem;
            font-weight: bold;
            color: white !important;
        }
        p {
            color: hsl(0, 0%, 74%);
        }

    }
    .stepNumber {
        position: relative;
        margin-right: 1rem;
        font-size: 48px;
        font-weight: bold;
        color: white !important;
    }
    .startListBlock {
        position: relative;

    }
    .startListImg {
        width: 100%;
    }
    .startListBlockItem {

        // display: flex;
        // flex-direction: column;
        // justify-content: space-evenly;
        // align-items: center;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 3px;
        color: white;
        background-color: rgba(70, 70, 70, 0.9);
        padding: 6rem;
        width: 100%;
        max-width: 720px;
    }
    .startList {
     
        line-height: 2rem;
        // margin: auto;
        // border: 2px red solid;
    }
    .startContent {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        margin: auto;
        // border: 2px blue solid;
        height: 100%;
        max-width: 500px;
    }
    .tickedText {
        font-size: 19px;
    }

    
    
    

    

    @media (max-width: 500px) {
        .centeredBlockStyle {
            height: 450px;
        }
        .startList {
            max-width: 350px;
        }
    }

    @media (max-width: 600px) {
        .stepImgBlock {
            h1 {
                font-size: 16px;
            }
        }
        .tickedText  {
            font-size: 14px;
        }

    }


    @media (max-width: 900px) {
        .startListBlockItem {
            position: static;
            margin-top: -3px;
            padding: 2rem;
            max-width: 1000px;
            width: 92.7%;
        }
        .startContent {
            min-height: 500px;

        }
    }
    @media (max-width: 1300px) {
        .step {
            max-width: 350px;
        }
        .stepNumber {
            font-size: 42px;
        }
    }

    
}