.SelfEmployedTariffs {
    position: relative;
    transition: all 0.2s ease-in-out;
    // overflow-y: auto;
    color: black;

    


    .link {
        color: white;
        font-size: 1.5rem;
        font-weight: bold;
        cursor: pointer;
        margin-bottom: 2rem;
        transition: all 0.2s ease-in-out;

        &:hover {
            color: #807CFA;
        }
    }
    .linkArrow {
        color: #03EE65;
    }

    .modal {
        position: fixed;
        top: 5%;
        left: 5%;
        right: 5%;
        z-index: 9999;
        background-color: white;
        box-shadow: 0px 0px 10px 10px #aaaaaa;
        padding: 2rem;
        overflow-y: auto;
        max-width: 1400px;
        margin: 0 auto;


        @media (max-width: 900px) {
            position: relative;
            top: 0%;
            left: 0%;
            right: 0%;
            margin-top: 2rem;

        }


    }

    .modalBody {
        display: flex;
        // flex-wrap: wrap;
        justify-content: space-between;
        align-content: center;
        max-width: 1400px;
        margin: 0 auto;

        
    }

    .modalBodyItem {
        margin-right: 1rem;
        margin-bottom: 1rem;
        max-width: 45%;
        padding: 1rem;

        @media (max-width: 1500px) {
            max-width: 100%;
        }

    }

    .modalCloseButton {
        position: absolute;
        font-size: 2rem;
        right: 20px;
        top: 15px;
        cursor: pointer;
        transition: all 0.2s ease-in-out;

        &:hover {
            color: #A31FED;
        }
    }

    .percent {
        font-weight: bold;
        font-size: 2rem;
        margin-right: 1rem;
    }

    .grayText {
        color: gray;
        font-size: 1.5rem;
        font-weight: bold;
    }

    .tickedText {
        font-size: 1.5rem;

        
    }
   
    @media (max-width: 700px) {
        .modal {
            // max-width: 350px;
            margin-right: 2rem;
            padding: 1rem;
        }

    }

    @media (max-width: 900px) {

        .tickedText, .grayText {
            font-size: 1rem;
        }
        .modalBody {
            flex-wrap: wrap;
        }
    }

    @media (max-width: 1300px) {
        .tickedText {
            font-size: 1rem;
        }
    }



}