.Blog {

  .topImgBlock {
    position: relative;
    width: 100%;

    h1 {
      text-align: center;
    }

  }

  .topImg {
    width: 100%;
  }

  .newsBlock {
    justify-content: center;
    text-align: center;

  }

  .newsColumn {

    border-radius: 10%;
    transition: all 0.2s ease-in-out;
    margin-bottom: 1rem;


    &:hover {
      background-color: hsl(0, 0%, 80%);
    }
  }








}